import type {
  Plugin,
  IAgentRuntime,
  GenerateTextParams,
  TextEmbeddingParams,
  ObjectGenerationParams,
  ImageGenerationParams,
} from '@elizaos/core';

import { ModelType, logger } from '@elizaos/core';

import { GatewayProvider } from './providers/gateway-provider';
import {
  generateTextAction,
  generateImageAction,
  generateEmbeddingAction,
  listModelsAction,
} from './actions';

export const vercelAIGatewayPlugin: Plugin = {
  name: 'aigateway',
  description:
    'Universal AI Gateway integration - Access 100+ AI models through Vercel AI Gateway using the AI SDK',

  // Model handlers - this makes it a model provider
  models: {
    [ModelType.TEXT_SMALL]: async (runtime: IAgentRuntime, params: GenerateTextParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateTextSmall(params);
    },
    [ModelType.TEXT_LARGE]: async (runtime: IAgentRuntime, params: GenerateTextParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateTextLarge(params);
    },
    [ModelType.TEXT_EMBEDDING]: async (runtime: IAgentRuntime, params: TextEmbeddingParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateEmbedding(params);
    },
    [ModelType.IMAGE]: async (runtime: IAgentRuntime, params: ImageGenerationParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateImage({
        prompt: params.prompt,
        count: params.count,
        size: params.size,
      });
    },
    [ModelType.OBJECT_SMALL]: async (runtime: IAgentRuntime, params: ObjectGenerationParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateObjectSmall(params);
    },
    [ModelType.OBJECT_LARGE]: async (runtime: IAgentRuntime, params: ObjectGenerationParams) => {
      const provider = new GatewayProvider(runtime);
      return provider.generateObjectLarge(params);
    },
  },

  // Initialize function
  init: async (_config: Record<string, string>, runtime: IAgentRuntime) => {
    logger.info('[AIGateway] Plugin initializing...');

    // Read configuration settings
    const apiKey =
      runtime.getSetting('AI_GATEWAY_API_KEY') || runtime.getSetting('AIGATEWAY_API_KEY');
    const useOIDC = runtime.getSetting('AIGATEWAY_USE_OIDC');
    const baseURL = runtime.getSetting('AIGATEWAY_BASE_URL');

    logger.info(`[AIGateway] Base URL: ${baseURL}`);
    logger.info(`[AIGateway] API Key configured: ${!!apiKey}`);
    logger.info(`[AIGateway] OIDC enabled: ${useOIDC}`);
    logger.info(`[AIGateway] Available model types: ${Object.keys(ModelType).join(', ')}`);

    // Verify environment is set up for Vercel AI Gateway
    const hasApiKey = !!(apiKey || process.env.AI_GATEWAY_API_KEY);
    const hasOIDCToken = !!process.env.VERCEL_OIDC_TOKEN;

    if (!hasApiKey && !hasOIDCToken) {
      logger.warn(
        '[AIGateway] No authentication configured. Set AI_GATEWAY_API_KEY or run "vercel env pull" for OIDC token'
      );
    } else {
      logger.info(`[AIGateway] Authentication configured: ${hasApiKey ? 'API Key' : 'OIDC Token'}`);
    }

    // The AI SDK automatically handles routing to Vercel AI Gateway
    // when environment variables are properly set
    logger.info('[AIGateway] Plugin initialization complete');

    return undefined;
  },

  // Tests array (empty for now)
  tests: [],

  // Actions for explicit user commands
  actions: [generateTextAction, generateImageAction, generateEmbeddingAction, listModelsAction],
};

export default vercelAIGatewayPlugin;
